:root {
  --primary-bg-color: #F4C142;
  --secondary-bg-color: #194989;
  --tertiary-bg-color: #0E2A4F;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", "Roboto", sans-serif;
}


.report {
  width: 100%;
  min-height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  background-color: var(--primary-bg-color);
}

.table {
}

/* spacing */

table {
  table-layout: fixed;
  width: 30em;
  border: 3px solid black;
  margin: 30px;
}

thead th:nth-child(1) {
  width: 60%;
}

thead th:nth-child(2) {
  width: 40%;
}

th, td {
  padding: 20px;
  border-bottom: 1px solid #ddd;
}

tr:hover {
  background-color: #f5f5f5;
}

.report_header {
  text-align: center;
  padding-top: 1em;
  padding-bottom: 1em;
  min-height: 4em;
}

.report-title {
  margin: 0;
  line-height: 1em;
  font-size: 1.3em;
}

.button-container {
  text-align: center;
}

.revenue-section, .expenses-section {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack:  center;
      justify-content:  center;
  background-color: white;
}

.doughnut-chart-container {
  margin: 1em;
}

.doughnut_chart {
  height: 18em;
}

button {
  color: var(--secondary-bg-color);
  font-size: 1.2em;
  font-weight: 700;
  background: none;
  border: 2px solid var(--secondary-bg-color);
  border-radius: 20px;
  padding: 0.3em 1em 0.3em 1em;
}

button:focus,
.link-btn:focus {
  outline: 1px dashed var(--secondary-bg-color);
}

button:hover, button:active, .link-btn:hover, .link-btn:active {
    color: var(--teriary-bg-color);
    border-color: var(--teriary-bg-color);
}

button:disabled, .link-btn:disabled {
    color: grey;
    border-color: grey;
}


